import React, { Component } from "react";

export default class Carousel extends Component {
  static defaultProps = {
    defaultDescription: "asdfasdf default description",
    data: []
  }

  constructor(props) {
    super(props)
    this.state = {
      activeEl: null
    }

    this.onMouseEnter = this.onMouseEnter.bind(this);
    this.onMouseLeave = this.onMouseLeave.bind(this);
  }

  onMouseEnter(i) {
    return event => {
      this.setState({activeEl: i})
    }
  }

  onMouseLeave(event) {
    this.setState({activeEl: null})
  }

  render() {
    const {activeEl} = this.state
    const data = this.props.data

    return (
      <div>
        <div className="flex flex-wrap py-12 mt-32 items-end">
          {this.props.data.map((c, i) => (
            <div key={`carousel_${i}`} className="w-1/5 text-center first:opacity-50 last:opacity-50">
              <img className="inline transition ease-out duration-700 transform hover:scale-125" style={{maxWidth: "100px", maxHeight:"300px"}} src={c.img} onMouseEnter={this.onMouseEnter(i)} onMouseLeave={this.onMouseLeave}/>
            </div>
          ))}
        </div>
        <div className="w-full my-24 text-center justify-center text-sm">
          <p className="max-w-md mx-auto">{activeEl !== null ? data[activeEl].description : this.props.defaultDescription}</p>
        </div>
      </div>
    )
  }
}
